.gallery {
    background: $color-blue-dark;
    grid-column: main-start/main-end;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(7, 5vw);
    grid-gap: 1.5rem;
    padding: 1.5rem;
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover; }

    &__item-1 {
        grid-column: 1/3;
        grid-row: 1/3; }
    &__item-2 {
        grid-column: 3/6;
        grid-row: 1/4; }
    &__item-3 {
        grid-column: 6/7;
        grid-row: 1/3; }
    &__item-4 {
        grid-column: 7/9;
        grid-row: 1/3; }
    &__item-5 {
        grid-column: 1/3;
        grid-row: 3/6; }
    &__item-6 {
        grid-column: 3/5;
        grid-row: 4/6; }
    &__item-7 {
        grid-column: 5/6;
        grid-row: 4/5; }
    &__item-8 {
        grid-column: 6/8;
        grid-row: 3/5; }
    &__item-9 {
        grid-column: 8/9;
        grid-row: 3/6; }
    &__item-10 {
        grid-column: 1/2;
        grid-row: 6/8; }
    &__item-11 {
        grid-column: 2/4;
        grid-row: 6/8; }
    &__item-12 {
        grid-column: 4/5;
        grid-row: 6/8; }
    &__item-13 {
        grid-column: 5/8;
        grid-row: 5/8; }
    &__item-14 {
        grid-column: 8/9;
        grid-row: 6/8; } }


